const buttons = document.querySelectorAll(".card button");
const innermodal = document.querySelector(".inner");
const outermodal = document.querySelector(".outer");
const x = window.matchMedia("(max-width: 450px)");

function handlingbuttons(event) {
  const button = event.currentTarget;
  const show = button.closest(".card");
  const img = show.querySelector("img").src;
  const wid = show.querySelectorAll("img").width;
  const des = show.dataset.description;
  const name = show.querySelector("h2").textContent;
  innermodal.innerHTML = `
  <img width="auto" height="550" src="${img}" alt="${name}">
  <p>${des}</p>
  `;
  outermodal.classList.add("open");
}

buttons.forEach((button) => button.addEventListener("click", handlingbuttons));

function closemodal() {
  outermodal.classList.remove("open");
}

outermodal.addEventListener("click", function (event) {
  const clickoutside = !event.target.closest(".inner");
  if (clickoutside) {
    closemodal();
  }
});

window.addEventListener("keydown", function (event) {
  if (event.key === "Escape") {
    closemodal();
  }
});
